@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Product+Sans);
.headerContainer {
  width: 100vw;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.headerContainer a {
  font: 13px arial, sans-serif;
  color: rgb(46, 46, 46);
  cursor: pointer;
}
.headerContainer a:hover {
  -webkit-text-decoration: underline solid rgb(46, 46, 46);
          text-decoration: underline solid rgb(46, 46, 46);
}
.userIcon {
  background-image: url(/google-ux-clone/static/media/Akali.ac2eb013.jpeg);
  background-size: cover;
  background-position: bottom;
  border-radius: 50%;
  background-color: white;
  width: 35px;
  height: 35px;
  position: absolute;
  right: 35px;
}

#gmail {
  position: absolute;
  right: 210px;
}

#img {
  position: absolute;
  right: 135px;
  cursor: pointer;
}

#gridIcon {
  position: absolute;
  right: 90px;
  color: rgb(115, 115, 115);
  cursor: pointer;
}
#gridIcon:hover {
  color: rgb(46, 46, 46);
}

.logoContainer span {
  font: 600 90px 'Product Sans', sans-serif;
}
#e {
  font: 600 100px 'Josefin Sans', sans-serif;
}

.bodyContainer {
  padding-bottom: 71px;
  width: 100vw;
  height: 75vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.searchContainer {
  margin-top: 25px;
  width: 482px;
  height: 44px;
  border: 1px solid rgb(214, 214, 214);
  border-radius: 20px;
  outline: none;
  display: grid;
  grid-template-columns: 10% 70% 10% 10%;
}
.searchContainer:hover {
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.37);
}
.searchContainer:focus {
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.37);
}
.searchIconContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.searchContainer #search {
  color: rgb(134, 134, 134);
}
.inputTextContainer input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 16px;
}
.keyboardContainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.keyboardContainer #keyboard {
  color: rgb(105, 105, 105);
}

.micContainer {
  max-height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.micContainer svg {
  height: 25px;
}

.buttonsContainer {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 18px;
}

.buttonsContainer button {
  color: rgb(51, 51, 51);
  height: 36px;
  margin: 11px 4px;
  background-color: #f8f8fa;
  border: 1px solid #f8f8fa;
  font-family: arial, sans-serif;
  font-size: 14px;
  outline: none;
  border-radius: 4px;
  padding: 0 16px;
  line-height: 27px;
  min-width: 54px;
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.buttonsContainer button:hover {
  border: 1px solid rgb(202, 202, 202);
}
.textContainer span {
  display: inline-block;
  line-height: 28px;
  font-family: arial, sans-serif;
  font-size: small;
  margin-top: 5px;
}
span span {
  color: rgb(0, 0, 196);
  cursor: pointer;
}
span span:hover {
  color: blue;
  -webkit-text-decoration: underline solid blue;
          text-decoration: underline solid blue;
}

.footerContainer {
  margin-top: 68px;
  width: 100vw;
  height: 10vh;
  background-color: #f2f2f2;
}
.regionContainer {
  font-family: arial, sans-serif;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.486);
  border-top: 1px solid rgba(0, 0, 0, 0.116);
  border-bottom: 1px solid rgba(0, 0, 0, 0.116);
  width: 100vw;
  height: 42px;
  display: flex;
  align-items: center;
  padding-left: 27px;
}

.policesContainer {
  display: flex;
  flex-direction: row;
}
.policesContainerOne {
  width: 100vw;
  font-family: arial, sans-serif;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.651);
  display: flex;
}
.policesContainerOne span {
  margin: 15px;
  margin-left: 25px;
}

.policesContainerTwo {
  width: 40vw;
  font-family: arial, sans-serif;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.651);
  display: flex;
  align-items: right;
}
.policesContainerTwo span {
  margin: 15px;
  margin-left: 25px;
}
@media (max-width: 774px) {
  .footerContainer {
    width: 100vw;
    height: 40vh;
    background-color: #f2f2f2;
  }
  .regionContainer {
    font-family: arial, sans-serif;
    font-size: 15px;
    color: rgba(0, 0, 0, 0.486);
    border-top: 1px solid rgba(0, 0, 0, 0.116);
    border-bottom: 1px solid rgba(0, 0, 0, 0.116);
    width: 100vw;
    height: 42px;
    display: flex;
    align-items: center;
    padding-left: 27px;
  }

  .policesContainer {
    display: flex;
    flex-direction: column;
  }
  .policesContainerOne {
    width: 100vw;
    height: 40px;
    font-family: arial, sans-serif;
    font-size: 15px;
    justify-content: center;
    color: rgba(0, 0, 0, 0.651);
    align-items: center;
    display: flex;
  }
  .policesContainerOne span {
    margin: 15px;
  }

  .policesContainerTwo {
    width: 100vw;
    height: 30px;
    font-family: arial, sans-serif;
    font-size: 15px;
    justify-content: center;
    color: rgba(0, 0, 0, 0.486);
    align-items: center;
    display: flex;
  }
  .policesContainerTwo span {
    margin: 15px;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App {
  overflow: hidden;
}

