.footerContainer {
  margin-top: 68px;
  width: 100vw;
  height: 10vh;
  background-color: #f2f2f2;
}
.regionContainer {
  font-family: arial, sans-serif;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.486);
  border-top: 1px solid rgba(0, 0, 0, 0.116);
  border-bottom: 1px solid rgba(0, 0, 0, 0.116);
  width: 100vw;
  height: 42px;
  display: flex;
  align-items: center;
  padding-left: 27px;
}

.policesContainer {
  display: flex;
  flex-direction: row;
}
.policesContainerOne {
  width: 100vw;
  font-family: arial, sans-serif;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.651);
  display: flex;
}
.policesContainerOne span {
  margin: 15px;
  margin-left: 25px;
}

.policesContainerTwo {
  width: 40vw;
  font-family: arial, sans-serif;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.651);
  display: flex;
  align-items: right;
}
.policesContainerTwo span {
  margin: 15px;
  margin-left: 25px;
}
@media (max-width: 774px) {
  .footerContainer {
    width: 100vw;
    height: 40vh;
    background-color: #f2f2f2;
  }
  .regionContainer {
    font-family: arial, sans-serif;
    font-size: 15px;
    color: rgba(0, 0, 0, 0.486);
    border-top: 1px solid rgba(0, 0, 0, 0.116);
    border-bottom: 1px solid rgba(0, 0, 0, 0.116);
    width: 100vw;
    height: 42px;
    display: flex;
    align-items: center;
    padding-left: 27px;
  }

  .policesContainer {
    display: flex;
    flex-direction: column;
  }
  .policesContainerOne {
    width: 100vw;
    height: 40px;
    font-family: arial, sans-serif;
    font-size: 15px;
    justify-content: center;
    color: rgba(0, 0, 0, 0.651);
    align-items: center;
    display: flex;
  }
  .policesContainerOne span {
    margin: 15px;
  }

  .policesContainerTwo {
    width: 100vw;
    height: 30px;
    font-family: arial, sans-serif;
    font-size: 15px;
    justify-content: center;
    color: rgba(0, 0, 0, 0.486);
    align-items: center;
    display: flex;
  }
  .policesContainerTwo span {
    margin: 15px;
  }
}
